a {
    text-decoration: none;
    color: black;
}

* {
    box-sizing: border-box;
}
.collection-page {
  display: flex;
  flex-direction: column; }
  .collection-page .title {
    font-size: 38px;
    margin: 0 auto 30px; }
  .collection-page .items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 10px; }
    .collection-page .items .collection-item {
      margin-bottom: 30px; }

.sign-in-and-sign-up {
  width: 850px;
  display: flex;
  justify-content: space-between;
  margin: 30px auto; }

.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0; }
  .checkout-page .checkout-header {
    width: 100%;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgrey; }
    .checkout-page .checkout-header .header-block {
      text-transform: capitalize;
      width: 23%; }
      .checkout-page .checkout-header .header-block:last-child {
        width: 8%; }
  .checkout-page .total {
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px; }
  .checkout-page button {
    margin-left: auto;
    margin-top: 50px; }
  .checkout-page .test-warning {
    margin-top: 20px;
    color: red; }

